import { Component } from '@angular/core';
import {
  NavController,
  LoadingController,
  Loading,
  AlertController,
  Alert
} from 'ionic-angular';
import { AnalisisProvider } from '../../providers/analisis/analisis';
import { Response } from '@angular/http';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  url: string = '';
  loading: Loading;
  alert: Alert;
  constructor(
    public navCtrl: NavController,
    private analisisPrv: AnalisisProvider,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController
  ) {}
  analizarUrl() {
    if (this.url.length > 0) {
      this.getObtenerDatos();
    } else {
      this.alert = this.alertCtrl.create({
        subTitle: 'Error',
        message: 'Ingrese una URL valida',
        buttons: ['OK']
      });
      this.alert.present();
    }
  }

  getObtenerDatos() {
    this.showLoading();
    let urlbase64 = btoa(this.url.trim());
    this.analisisPrv.obtenerDatosUrl(urlbase64).subscribe(
      (data: Response) => {
        this.loading.dismiss();
        console.log(data.json());
        this.navCtrl.push('ResultadosPage', {
          id: data.json().id,
          url: this.url.trim()
        });
      },
      error => {
        this.loading.dismiss();
        console.log(error);
      }
    );
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Analizando...'
    });
    this.loading.present();
  }
}



